import React, { useState, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import { loadIcon } from "./LoadIcon";

const dispatchedEvents = [];

const SrsMuiBottomNavigationAction = function (props, ref) {
  const [iconComp, setIconComp] = useState(null);
  const Icon = iconComp;

  useEffect(() => {
    if (props.icon) {
      loadIcon(props.icon, setIconComp);
    }
  }, [props.icon]);

  return (
    <BottomNavigationAction
      icon={Icon ? <Icon /> : null}
      label={props.label}
      value={props.value}
      disabled={props.disabled}
      showLabel={props.showLabel}
      selected={props.selected}
      onChange={props.onChange}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiBottomNavigationAction component */
`;

const props = [
  {
    name: "icon",
    defaultValue: "",
    type: "string",
  },
  {
    name: "label",
    defaultValue: "",
    type: "string",
  },
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "showLabel",
    defaultValue: true,
    type: "boolean",
  },
  {
    name: "selected",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiBottomNavigationAction),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
